.App {
  margin: 0 auto;
}

.header {
  background-color: #eee;
  padding: 5px 0;
}

.btns {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 9;
  left: 0;
  background-color: rgba(0,0,0,.4);
}



.btns button {
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 14px;
  display: block;
  border: none;
  padding: .6rem 0;
  outline: none;
  background-color: transparent;
  color: #fff;
}


.btns button:active {
  opacity: .8;
}

.title, .info {
  text-align: center;
}

.title {
  font-size: 16px;
  margin: 0;
}

.info {
  margin-top: 0;
  margin-bottom: 0;
}

@media screen and (min-width: 480px) {
 .App {
   max-width: 360px;
 } 
}


